import React, { memo, useEffect, useState } from "react";
import TextInput from "./../Partials/TextInput";
import ButtonSave from "./../Partials/ButtonSave";
import UploadedFileComponent from "./../Partials/FileUploaded";
// import QuestionOptions from "./_Options";
import QuestionAnswer from "./_Answer";
import Btns from "./../Partials/ButtonSave";
import $ from "jquery";
import M from "materialize-css";
import {AddTest} from './../../../../../../../entity/Redux/Admin/Action';
import {useDispatch, useSelector} from 'react-redux'

export default memo(() => {
  const [testDataAll, setTestDataAll] = useState({
    type: 3,
    items : [],
    answers : []
  });

  const dispatch = useDispatch();
  const FILE_CODE = "clinic_img_1254";
  const [optionsss, setOptionsss] = useState({
    option: [],
  });
  // add items
  const [items, setItems] = useState({});
  // add question options
  const [question, setQuestion] = useState();
  const [answer, setAnswer] = useState({});
  useEffect(() => {
    var elems = document.querySelectorAll(".collapsible");
    M.Collapsible.init(elems);
  });

  let option = [];
  let answers = [];
  const HandleChangeInput = (e) => {
    setQuestion({
      ...question,
      [e.target.name]: e.target.value,
    });
  };

  const changeTestInput = (e) => {
    setTestDataAll({
      ...testDataAll,
      [e.target.name]: e.target.value,
    });
  };

  const AddNewQuestion = (e) => {
    e.preventDefault(e);
  };

  const [finalData , setFinalData] = useState({});

  let check = true;
  let counter = 0;
  const HandleForm = (e) => {
    e.preventDefault();
    let AllData = { ...testDataAll };
    console.log("test data all before : ", testDataAll);

 

      if (optionsss.option.length == 2) {
        optionsss.option.map((item) => {
          counter++;
        });
        
        console.log("counter : ", counter);
  
        if (counter == 2 && answer.name != "" && answer.total != "") {
          let answerParse = JSON.parse(answer);
          let newAnswerParse = {...answerParse};
          newAnswerParse.totoal = parseInt(answerParse.totoal);
          let answerob = { ...newAnswerParse };
          answerob.itemType = 2;
          let questions = { ...question };
          questions.options = optionsss.option;
          AllData.items[AllData.items.length] = questions;
          AllData.answers[AllData.answers.length] = answerob;
          console.log("all data test : ", AllData);
          optionsss.option = [];
          setAnswer({});
          $("input[name=name]").val("");
          $("input[name=text]").val("");
          $("input[name=total]").val("");
          $("textarea[name=text]").val("");
          $("input[name=question]").val("");
        } else {
          console.log("option data is not full : ");
        }
      } else {
        console.log("option data is not full : ");
      }
  
    }
    


    // console.log("option data : " , question);
  const SaveTestFinal = (e) => {
    e.preventDefault();
    let docPath = localStorage.getItem(FILE_CODE);
    let filePathUp = JSON.parse(docPath).data.data;
    let testDataFin = {...testDataAll};
    testDataFin.imageUrl = filePathUp;
    console.log("all data : " , testDataFin); 
    dispatch(AddTest(testDataFin));
  }

  return (
    <div
      className="col s12"
      style={{
        position: "relative",
        paddingTop: "20px",
        paddingBottom: "20px",
      }}
    >
      <div
        className="col s12"
        style={{
          marginTop: "20px",
          borderBottom: "1px solid #4ce6fc",
          paddingBottom: "25px",
        }}
      >
        <form onSubmit={(e) => HandleForm(e)}>
          <div className="col s12 l4 m4">
            <TextInput
              title="Test name"
              type="text"
              name="name"
              placeholder="enter test name"
              onChange={(e) => changeTestInput(e)}
            />
          </div>
          <div className="col s12 l8 m8">
            <TextInput
              type="text"
              title="Test description"
              name="description"
              placeholder="enter test description"
              onChange={(e) => changeTestInput(e)}
            />
          </div>

          <div className="container">
            <div className="col s12 l6 m6">
              <UploadedFileComponent title="question img" placeholder="file" />
            </div>
          </div>
        </form>
      </div>

      <ul className="collapsible customs-colapse">
        <li className="active">
          <div className="collapsible-header">
            <i className="material-icons">beenhere</i>
            Create item for test type 3
          </div>
          <div className="collapsible-body">
            <span>
              <div className="container">
                <div className="row">
                  

                  <div className="col s12 l6 m6">
                    <div
                      className="col s12"
                      style={{
                        paddingBottom: 20,
                        marginBottom: 10,
                      }}
                    >
                      <TextInput
                        name="name"
                        id="changeInputs"
                        title="question option 1"
                        onChange={(e) =>
                          (optionsss.option[0] = {
                            name: e.target.value,
                            score: 1,
                          })
                        }
                        placeholder="Enter question option 1  ..."
                      />
                      <TextInput
                        name="name"
                        id="changeInputs"
                        title="question option 2"
                        onChange={(e) =>
                          (optionsss.option[1] = {
                            name: e.target.value,
                            score: 2,
                          })
                        }
                        placeholder="Enter question option 2 ..."
                      />
                     
                    </div>
                  </div>

                  <div className="col s12 l6 m6" style={{ paddingTop: 15 }}>
                    {/* <QuestionAnswer data={item => console.log("dataaaaaqa  : " , item)} /> */}
                    <QuestionAnswer data={(item) => setAnswer(item)} />
                  </div>

                  <div className="col s12">
                    <div className="col s4">
                      <Btns
                        onClick={(e) =>
                          HandleForm(e)
                        }
                        type="button"
                        value="Add Next"
                        className="btn waves-effect blue darken-4"
                      />
                    </div>

                    <div className="col s4">
                      <Btns
                        type="button"
                        value="Clear All"
                        className="btn waves-effect red darken-4"
                      />
                    </div>

                    <div className="col s4">
                      <Btns
                        type="button"
                        value="Save All"
                        onClick={(e) => SaveTestFinal(e)}
                        className="btn waves-effect green darken-4"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </span>
          </div>
        </li>
      </ul>
    </div>
  );
});
