import React, { memo } from "react";

export default memo((props) => {
  return (
    <div
      className="input-field"
      style={{ float: "left", width: "100%", marginTop: "40px" , borderRadius: '5px !important'}}
    >
      <input style={{ width: "100%" , textTransform: "unset" }} {...props} />
    </div>
  );
});
