import React, { memo, useEffect, useState } from "react";
import M from "materialize-css";
import $ from "jquery";
import { UploadedFile } from "../../../../../../../entity/Redux/Admin/Action";
import { useDispatch, useSelector } from "react-redux";
export default memo((props) => {
  const FILE_CODE = "clinic_img_1254";

  const dispatch = useDispatch();
  const [selectFile, setSelectFile] = useState();
  useEffect(() => {}, []);

  const handleChangeFile = (e) => {
    setSelectFile(e.target.files[0]);
  };

  const [filePath, setFIlePath] = useState({ isAssistant: false });

  function upload_file() {
    const formData = new FormData(); // new kardan formData
    $("#uploaded_img_clinic").fadeIn(); // fadeIn backLoading
    // if (selectFile) {
    // check konim ke file add shode ya na
    formData.append("file", selectFile); // append kardan filename be formdata

    $(".loading_progress").fadeIn();
    setTimeout(() => {
      dispatch(UploadedFile(formData));
    }, 300);
  }

  function submitUserDetail() {
    let docPath = localStorage.getItem(FILE_CODE);
    let filePathUp = JSON.parse(docPath).data.data;
    let attachment = [];
    attachment[0] = filePathUp;
    let dataNew = { ...filePath };
    dataNew.filePath = filePathUp;

    // dispatch(FitDataDoctor(dataNew));
  }

  return (
    <div
      className="file-field input-field"
      style={{
        position: "relative",
        float: "left",
        height: "80",
        width: "100%",
      }}
    >
      <span id="uploaded_img_clinic" style={{top : 7}}>
        <div class="progress" id="progress_img_clinic">
          <div class="indeterminate"></div>
        </div>
      </span>


      <p style={{ color: "#ccc" }}>&nbsp;&nbsp;{props.title}</p>
      <span id="uploaded_img_clinic">
        <div class="progress" id="progress_img_clinic">
          <div class="indeterminate"></div>
        </div>
      </span>

      <div className="btn" style={{ backgroundColor: "#00b8d4 !important" }}>
        <span>
          <i className="material-icons">attachment </i>
        </span>
        <input onChange={handleChangeFile} type="file" name="file" />
      </div>

      <div className="file-path-wrapper">
        <input
          className="file-path validate"
          type="text"
          placeholder={props.placeholder}
        />
      </div>

      <div
        id={"progresss"}
        className="hoc"
        onClick={(e) =>
          selectFile
            ? upload_file()
            : M.toast({ html: "please select file ..." })
        }
      >
        <span>
          <i className={"material-icons"}>upload</i>
        </span>
      </div>
    </div>
  );
});
