import React, { memo } from "react";

export default memo((props) => {
  return (
    <div>
      <div className="input-field col s12" style={{ marginTop: 15 }}>
        {/* <p style={{ color: "#ccc" }}>&nbsp;&nbsp;{props.title}</p> */}
        <div id="input_back">
          <textarea
            {...props}
          />
        </div>
      </div>
    </div>
  );
});
