import React, { memo, useEffect, useState } from "react";
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
import { Link, NavLink } from "react-router-dom";
import TestOne from "./TestComponents/Components/TestOne";
import TestTow from "./TestComponents/Components/TestTow";
import TestThree from "./TestComponents/Components/TestThree";
import TestFour from "./TestComponents/Components/TestFour";
import TestFive from "./TestComponents/Components/TestFive";
import $ from "jquery";
import M from "materialize-css";
export default memo(() => {
  useEffect(() => {
    var tabs = document.querySelectorAll(".tabs");
    M.Tabs.init(tabs);
  }, []);
  return (
    <div>
      <div id="loader-wrapper">
        <div id="loader"></div>
        <div className="loader-section section-left"></div>
        <div className="loader-section section-right"></div>
      </div>

      <Header />
      <div id="main">
        <div className="wrapper">
          <SideBar />

          <div id="back_shado"></div>
          <div className="container">
            <div className="row">
              <div className="col s12">
                <div id="breadcrumbs-wrapper">
                  <div className="container">
                    <div className="row">
                      <div className="col s10 m6 l6">
                        <h5 className="breadcrumbs-title">Doctors list</h5>
                        <ol className="breadcrumbs">
                          <li>
                            <Link to="/admin">
                              <a>Dashboard</a>
                            </Link>
                          </li>
                          <li>
                            <a className="active">Doctors list</a>
                          </li>
                        </ol>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col s12" style={{ marginBottom: 30 }}>
                <div
                  className="container"
                  style={{
                    borderRadius: "10px",
                    background: "#212121",
                    width: "100%",
                    overflow: "hidden",
                  }}
                >
                  <div className="row">
                    <ul id="tabs-swipe-demo" class="tabs">
                      <li class="tab col s2">
                        <a class="active" href="#test-swipe-1">Test 1</a>
                      </li>
                      <li class="tab col s2">
                        <a  href="#test-swipe-2">
                          Test 2
                        </a>
                      </li>
                      <li class="tab col s2">
                        <a href="#test-swipe-3">Test 3</a>
                      </li>
                      <li class="tab col s2">
                        <a href="#test-swipe-4">Test 4</a>
                      </li>
                      <li class="tab col s2">
                        <a href="#test-swipe-5">Test 5</a>
                      </li>
                    </ul>
                    <div id="test-swipe-1" class="col s12 ">
                      <TestOne />
                    </div>
                    <div id="test-swipe-2" class="col s12 ">
                      <TestTow />
                    </div>
                    <div id="test-swipe-3" class="col s12 ">
                      <TestThree />
                    </div>
                    <div id="test-swipe-4" class="col s12 ">
                      <TestFour />
                    </div>
                    <div id="test-swipe-5" class="col s12 ">
                      <TestFive />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
